import SVG from 'react-inlinesvg';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
const NoConnectionFound = ({ accountModalShow }) => {
    return <div className="block-center notFound_contain_ py-4" style={{ minHeight: 'calc(100vh - 320px)' }}>
        <div className="d-flex flex-column align-items-center" style={{ maxWidth: 450 }}>
            <div className="mb-3 mb-xl-4">
                <span className="svg-icon svg-icon-xxl me-0">
                    <SVG src={baseUrl('media/icons/svg-icons/account-placeholder.svg')} height={32} width={32} />
                </span>
            </div>
            <div className="mb-2 text-center">
                <h5 className="text-gray-dark fw-semibold text-center">Nothing to see here!</h5>
                <p className='fs-6'>Uh-oh! No social networks connected. Link your connections and collect more content!</p>
            </div>
            <button type="button" className="btn btn-primary mb-2" onClick={accountModalShow}>
                <i className="ri-add-line"></i>
                Add Social Connection
            </button>
        </div>
    </div>
}
export default NoConnectionFound;